  //     // set a unique ID
  //     newSearchControl.id = styles.SearchBox;

  //     // position current search control to float right
  //     searchContainer.classList.add(`${styles.ootbSearchBox}`);
  //     newSearchControl.querySelector("input").setAttribute("placeholder", "Advanced Intranet Search");

  //     let searchClearButton = newSearchControl.querySelector(`button[title^=Clear]`) as HTMLButtonElement;
  //     searchClearButton.style.backgroundColor = "rgba(0,0,0,0)";
  //     searchClearButton.style.border = "none";
