  //     // searchContainer.parentElement.appendChild(newSearchControl);

  //     // or add it to our placeholder location
  //     document.querySelector(`.${styles.SearchContainer}`).appendChild(newSearchControl);

  //     // reset the container height when the second one is added
  //     searchContainer.style.height = `${containerHeight}px`;

  //     this.HandleSearchEvents();
